<template>
  <div id="musicBar" v-if="audio_data">

    <!-- sound -->
    <div class="sound">
      <audio id="audio" autoplay :src="audio_data.sound.source"></audio>
      <mu-container>
        <mu-row gutter>
          <mu-col span='2' class="sound-img">
            <router-link :to="{name: 'Detail', params: {id: audio_data.sound.id}}">
              <img class="img" :src="audio_data.sound.pic_100" alt="">
            </router-link>
          </mu-col>
          <mu-col span='5' class="sound-info">
            <p>{{audio_data.sound.name}}</p>
            <p>{{audio_data.sound.user.name}}</p>
          </mu-col>
          <mu-col span='5'>
            <mu-row class="control">
              <!--  -->
              <mu-col span='4' tag='span'>
                <mu-button icon @click.stop="$refs.sheet.openSheet()">
                  <font-awesome-icon icon="list-ul" />
                </mu-button>
              </mu-col>
              <!--  -->
              <mu-col span='4' tag='span'>
                <mu-button icon @click.stop="set_audio_play(!audio_play)">
                  <font-awesome-icon :icon="audio_play ? 'pause' : 'play'" />
                </mu-button>
              </mu-col>
              <!--  -->
              <mu-col span='4' tag='span'>
                <mu-button icon @click.stop="nextSound()">
                  <font-awesome-icon icon="step-forward" />
                </mu-button>
              </mu-col>
            </mu-row>
          </mu-col>
        </mu-row>
      </mu-container>
    </div>

    <!--  -->
    <div class="progress-bar">
      <mu-flex>
        <mu-linear-progress mode="determinate" :size="2" color="green" :value="$store.getters.audio_progress"/>
      </mu-flex>
    </div>

    <!--  -->
    <Sheet ref="sheet"></Sheet>

  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'MusicBar',
  data () {
    return {
    }
  },
  // 
  computed: {
    // mapState
    ...mapState([
      'audio',
      'playMode',
      'playList'
    ]),
    ...mapState({
      audio_data: state => state.audio.data,
      audio_play: state => state.audio.play
    })
  },
  // 
  watch: {
    //  `audio_data` 
    audio_data (val) {
      if (val) {
        this.$nextTick(() => {
          this.audio_init()
        })
      }
    },
    audio_play (val) {
      val ? this.audio.ele.play() : this.audio.ele.pause()
    }
  },
  // 
  methods: {
    // mapMutations
    ...mapMutations([
      'set_audio_data',
      'set_audio_ele',
      'set_audio_play',
      'set_audio_duration',
      'set_audio_currentTime'
    ]),

    // 
    audio_init () {
      console.log('init')

      let _audio = this.$el.querySelector('#audio')
      this.set_audio_ele(_audio) // 

      // /
      _audio.oncanplay = () => {
        _audio.play()
        this.set_audio_duration(_audio.duration)
      }
      // 
      //  ~~ 
      _audio.ontimeupdate = () => {
        this.set_audio_currentTime(_audio.currentTime)
      }
      // 
      _audio.onplay = () => {
        this.set_audio_play(true)
      }
      // 
      _audio.onpause = () => {
        this.set_audio_play(false)
      }
      // 
      _audio.onended = () => {
        this.set_audio_play(false)
      }
    },

    // 
    nextSound () {
      // playLiscurrentIndex
      // findIndex()-1
      let currentIndex = this.playList.findIndex((n) => n.sound.id === this.audio.data.sound.id)
      // currentIndex 0   +1
      if (currentIndex > -1) { // 
        let nextIndex
        currentIndex === this.playList.length - 1 ? nextIndex = 0 : nextIndex = currentIndex + 1
        if (this.playList[nextIndex].sound.id === this.audio.data.sound.id) {
          this.audio.ele.load()
          this.audio.ele.play()
        } else {
          this.set_audio_data(this.playList[nextIndex])
        }
      } else {
        // 
        console.warn('Playlist is empty')
      }
    }

  }

}
</script>

<style lang="stylus">
// 
#musicBar
  width 100%
  position fixed
  bottom 0
  z-index 20180706
  background #fff
  height toRem(50)
  &:before
    content ' '
    position absolute
    top 0
    height 1px
    width 100%
    background #e6e6e6
    display inline-block
  .sound
    .sound-img
      padding-right 0
      padding-top toRem(6)
      img
        width toRem(36)
        height toRem(36)
    .sound-info
      padding-left 0
      margin-left -8px
      padding-top toRem(7)
      p
        font-size toRem(12)
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
    .control
      .col
        text-align center
        color #666
        svg
          font-size toRem(16)
  .progress-bar
    position absolute
    bottom 0
    left  0
    width 100%
    height toRem(2)
    z-index 10001
</style>



